import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
import BootstrapVue from "bootstrap-vue"
import VueRouter from 'vue-router'
import App from './App.vue'
import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap-vue/dist/bootstrap-vue.css"
import routes from './router'

Vue.config.productionTip = false
Vue.use(BootstrapVue)
Vue.use(VueRouter)
Vue.use(Vuex)

const router = new VueRouter({
  routes,
  mode: "history",
})

const vuexSession = new VuexPersistence({
  storage: window.sessionStorage
})

const store = new Vuex.Store({
  state: {
    activeUser: null,
    institution: null,
    loggedInFamily: null,
    loggedInMemberID: "",
    loggedInMember: null,
    loadedFamily: null,
  },
  mutations: {
    setActiveUser (state, u) {
      state.activeUser = u
    },
    setInstitution (state, i) {
      state.institution = i
    },
    setLoadedFamily (state, f) {
      state.loadedFamily = f
    },
    setLoggedInFamily (state, f) {
      state.loggedInFamily = f
    },
    setLoggedInMemberID(state, m) {
      state.loggedInMemberID = m
    },
    setLoggedInMember(state, m) {
      state.loggedInMember = m
    },
    resetLoadedFamily (state) {
      console.log('loadedFamily reset to null')
      state.loadedFamily = null
    },
    resetLoggedInFamily (state) {
      console.log('loggedInFamily reset to null')
      state.loggedInFamily = null
    },
    resetLoggedInMemberID (state) {
      console.log('loggedInGaurdian reset to empty')
      state.loggedInMemberID = ""
    },
    resetLoggedInMember (state) {
      console.log('loggedInMember reset to null')
      state.loggedInMember = null
    }
  },
  plugins: [vuexSession.plugin]
})

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
